label.labelReview{
    width: 18px;
    height: 18px;
    border:1px solid #000;
    display: inline-block;
    position: absolute;
    left: 12px;
    top: 50%;
    transform: translateY(-50%);
}
input.inputReview{
  opacity: 0;
  display: none;
}
input.inputReview:checked + label.labelReview{
  width: 18px;
  height: 18px;
  border:1px solid #000;
  display: inline-block;
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
}
input.inputReview:checked + label.labelReview::after{
  width: 12px;
  height: 12px;
  position: absolute;
  top:50%;
  left: 50%;
  content: "";
  transform: translate(-50%, -50%);
  background-color: #000;
}

@media (max-width: 1024px) {
  label.labelReview{
    width: calc(100vw*(18/428));
    height: calc(100vw*(18/428));
    left: calc(100vw*(0/428));
  }

  input.inputReview:checked + label.labelReview{
    width: calc(100vw*(18/428));
    height: calc(100vw*(18/428));
    left: calc(100vw*(0/428));
  }
  input.inputReview:checked + label.labelReview::after{
    width: calc(100vw*(12/428));
    height: calc(100vw*(12/428));
  }
}