/* Reset */
/*보편적 스타일링 reset.css*/
*{-webkit-tap-highlight-color:none;
  font-family: 'Avenir', 'HelveticaNeue', 'Big Caslon',  'SF ProText', sans-serif;
  color:#000;
  outline:none;}
  html, body,
  div, span,
  dl, dt, dd, ul, ol, li,
  h1, h2, h3, h4, h5, h6,
  blockquote, p, address, pre, cite,
  form, fieldset, input, textarea, select,
  table, th, td {
    margin:0;
    padding:0;
    font-weight:700;
    outline:none;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"]::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
  }
  input::placeholder{font-family:'Avenir', 'HelveticaNeue', 'Big Caslon',  'SF ProText';font-weight:700;}
  h1, h2, h3, h4, h5, h6 {
    font-size:100%;
    font-weight:700;
    letter-spacing:normal;
  }
  button{
    outline: none;
    background-color: #fff;
    cursor: pointer;
  }
  fieldset, img, abbr,acronym { border:0 none; }
  
  ol, ul { list-style:none; }
  
  table {
    border-collapse: separate;
    border-spacing:0;
    border:0 none;
  }
  caption, th, td {
    text-align:left;
    font-weight: 700;
    border:0;
  }
  
  address, caption, strong, em, cite {
    font-weight:700;
    font-style:normal;
  }
  ins { text-decoration:none; }
  del { text-decoration:line-through; }
  
  blockquote:before, blockquote:after, q:before, q:after { content:""; }
  blockquote,q { quotes:"" ""; }
  
  hr { display:none; }

  a{
    text-decoration: none;
  }


/* swiper 버튼 x */
div.swiper-button-prev, div.swiper-button-next, span.swiper-pagination-bullet{
  display: none;
  opacity: 0;
}
