

label.label{
    width: 12px;
    height: 12px;
    border:1px solid #000;
}
input.input{
  opacity: 0;
  display: none;
}
input.input:checked + label.label{
  background-color: #fff;
  position:relative
}
input.input:checked + label.label::after{
  width: 7px;
  height: 7px;
  position: absolute;
  top:50%;
  left: 50%;
  content: "";
  transform: translate(-50%, -50%);
  background-color: #000;
}

