

label.caseLabel{
    width: 12px;
    height: 12px;
    border:1px solid #000;
    border-radius: 50%;
  }
input.caseInput{
    opacity: 0;
    display: none;
}
input.caseInput:checked + label.caseLabel{
    background-color: #fff;
    position:relative
}
input.caseInput:checked + label.caseLabel::after{
    width: 7px;
    height: 7px;
    position: absolute;
    top:50%;
    left: 50%;
    content: "";
    transform: translate(-50%, -50%);
    background-color: #000;
    border-radius: 50%;
}